<template>
  <div class="home">
    <div class="sidebar" v-bind:class="{show: show}" >
      <ul>
        <li class="about-me">
          <a v-on:click="aboutMe">About Me</a>
        </li>
        <li class="my-skills">
          <a v-on:click="mySkills">My Skills</a>
        </li>
        <li class="my-projects">
          <a v-on:click="myProjects">My Projects</a>
        </li>
        <li class="achievments">
          <a v-on:click="achiev">Achievments</a>
        </li>
      </ul>
    </div>
    <div class="content" v-bind:class="{show: show}" >
      <div v-bind:class="{show: aboutMeShow}">
        <div class="cntnt">
          <h1>About Me</h1>
        </div>
        <button v-on:click="closeContent">
          <div></div>
          <div></div>
        </button>
      </div>
      <div v-bind:class="{show: mySkillsShow}">
        <div class="cntnt">
          <h1>My Skills</h1>
        </div>
        <button v-on:click="closeContent">
          <div></div>
          <div></div>
        </button>
      </div>
      <div v-bind:class="{show: myProjectsShow}">
        <div class="cntnt">
          <h1>My Projects</h1>
        </div>
        <button v-on:click="closeContent">
          <div></div>
          <div></div>
        </button>
      </div>
      <div v-bind:class="{show: achievmentsShow}">
        <div class="cntnt">
          <h1>My Achievments</h1>
        </div>
        <button v-on:click="closeContent">
          <div></div>
          <div></div>
        </button>
      </div>
    </div>
    <div class="header">
      <h1 class="firstWord">
        I'm
        <span class="secondWord">Muchlis Kurnia</span>
      </h1>
      <h2>
        Embedded System, Machine Learning, Web Development.
      </h2>
      <div class="icon">
        <a href="#" class="fa fa-facebook fa-2x"></a>
        <a href="#" class="fa fa-twitter fa-2x"></a>
        <a href="#" class="fa fa-linkedin fa-2x"></a>
        <a href="#" class="fa fa-github fa-2x"></a>
      </div>
    </div>
    <div class="nav-button">
      <button v-bind:class="{show: show}" v-on:click="btnClick">
        <div></div>
        <div></div>
        <div></div>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data: function() {
    return {
      show: false,
      aboutMeShow: false,
      mySkillsShow: false,
      myProjectsShow: false,
      achievmentsShow: false
    }
  },
  methods: {
    btnClick: function() {
      this.show = !this.show;
    },
    aboutMe: function() {
      this.aboutMeShow = !this.aboutMeShow;
      this.mySkillsShow = false;
      this.myProjectsShow = false;
      this.achievmentsShow = false;
    },
    mySkills: function() {
      this.aboutMeShow = false;
      this.mySkillsShow = !this.mySkillsShow;
      this.myProjectsShow = false;
      this.achievmentsShow = false;
    },
    myProjects: function() {
      this.aboutMeShow = false;
      this.mySkillsShow = false;
      this.myProjectsShow = !this.myProjectsShow;
      this.achievmentsShow = false;
    },
    achiev: function() {
      this.aboutMeShow = false;
      this.mySkillsShow = false;
      this.myProjectsShow = false;
      this.achievmentsShow = !this.achievmentsShow;
    },
    closeContent: function() {
      this.aboutMeShow = false;
      this.mySkillsShow = false;
      this.myProjectsShow = false;
      this.achievmentsShow = false;
    }
  }
}
</script>

<style scoped>
@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css";
</style>

<style lang="scss" scoped>
@import "../../assets/scss/home.scss";
</style>




